<template>
  <div :class="css.wrapper">
    <button
      type="button"
      v-for="tab in tabs"
      :key="tab.id"
      @click="chooseTab($event, tab.id)"
      :class="[css.tab, value === tab.id && css.activeTab]"
    >
      {{ tab.name }}
    </button>
    <div :class="css.line" :style="{ left: this.left }"></div>
  </div>
</template>

<script>
import css from './AuthPage.css';
export default {
  props: {
    tabs: {
      type: Array,
      default: () => [
        {
          id: 0,
          name: ''
        },
        {
          id: 1,
          name: ''
        }
      ]
    },
    value: {
      type: Number
    }
  },
  data() {
    return {
      css,
      activeTab: 0,
      left: 0
    };
  },
  methods: {
    chooseTab(e, id) {
      this.left = this.left = `${e.target.offsetLeft}px`;
      this.$emit('input', id);
      this.$emit('change', id);
    }
  }
};
</script>
