<template>
  <label :class="css.label" :for="name">
    <input
      :class="css.input"
      :id="name"
      :name="name"
      :type="type"
      :value="value"
      tabindex="1"
      :required="required"
      :placeholder="placeholder"
      :autocomplete="autocomplete"
      @input="$emit('input', $event.target.value)"
    />
  </label>
</template>

<script>
import css from './Input.css';
export default {
  name: 'Input',
  props: {
    value: String,
    type: {
      default: 'text',
      type: String
    },
    required: {
      default: false,
      type: Boolean
    },
    autocomplete: {
      default: 'off',
      type: String
    },
    name: {
      required: true,
      type: String
    },
    icon: {
      type: Boolean,
      default: false
    },
    placeholder: {
      type: String
    }
  },
  data() {
    return {
      css
    };
  },
  computed: {
    isFocus() {
      return this.value && this.value.length !== 0;
    }
  }
};
</script>
