<template>
  <div :class="css.wrapper">
    <div :class="css.container">
      <div :class="css.imageWrapper">
        <img
          src="./static/form-banner.png"
          data-src="./static/form-banner.png"
          alt=""
          :class="css.image"
          loading="lazy"
        />
      </div>
      <div :class="css.formWrapper">
        <div :class="css.tabsWrapper">
          <AuthTabs v-model="auth" />
        </div>
        <form
          ref="form"
          :class="[
            hasAnimateForm && (auth === 0 ? css.formLeft : css.formRight)
          ]"
          @submit.prevent="submit"
        >
          <div :class="css.marginBottom">
            <UiFormItem title=" " :error="error.username">
              <UiInput
                v-model="name"
                name="name"
                label=""
                type="name"
                autocomplete="off"
                placeholder=""
              />
            </UiFormItem>
          </div>
          <div :class="css.marginBottom">
            <UiFormItem title="" :error="error.password">
              <UiInput
                v-model="password"
                name="password"
                label="Password"
                type="password"
                placeholder="1234"
              />
            </UiFormItem>
          </div>
          <div :class="css.btnWrapper">
            <UiButton type="submit">
              
            </UiButton>
          </div>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
import { mapMutations } from 'vuex';
import AuthTabs from '@/components/AuthTabs';
import { FormItem, Button, Input } from '@/components/UI';
import { POST } from '@/services/api';
// import { DashboardPage } from '@/router/routing';

import css from './AuthPage.css';

export default {
  name: 'AuthPage',
  data() {
    return {
      css,
      name: '',
      password: '',
      error: {},
      auth: 0,
      loading: true,
      hasAnimateForm: false
    };
  },
  methods: {
    ...mapMutations(['USER_LOGIN']),
    async login(data) {
      try {
        const result = await POST('login', data);
        if (result.error) {
          this.error = result.error;
        } else {
          await this.$router.push('/');
        }
      } catch (e) {
        alert(e);
      }
    },
    async registration(data) {
      const result = await POST('registration', data);
      if (result.error) {
        this.error = result.error;
      } else {
        this.$router.push('/');
      }
    },
    async submit() {
      const data = {
        username: this.name,
        password: this.password
      };
      if (this.auth === 0) {
        await this.login(data);
      } else {
        await this.registration(data);
      }
    }
  },
  watch: {
    auth() {
      this.hasAnimateForm = true;
      this.error = {};
      this.name = '';
      this.password = '';
    }
  },
  components: {
    AuthTabs,
    UiButton: Button,
    UiInput: Input,
    UiFormItem: FormItem
  }
};
</script>
