<template>
  <div :class="css.wrapper">
    <div :class="css.title">
      Create Wallet
    </div>
    <div :class="css.flex">
      <div :class="css.card">
        <WalletCard
          :name="walletName"
          :type="walletType"
          :balance="walletBalance"
          :status="wallet.status"
        />
      </div>
      <form :class="css.form" @submit.prevent="submit">
        <UiFormItem :class="css.formItem" title=" ">
          <UiInput
            v-model="walletName"
            name=""
            placeholder="  "
          />
        </UiFormItem>
        <UiFormItem :class="css.formItem" title="">
          <UiInput
            v-model="walletBalance"
            type="number"
            name="balance"
            placeholder="   "
          />
        </UiFormItem>
        <UiFormItem title="  ">
          <UiDropdown
            v-model="walletType"
            :class="css.formItem"
            :dropdownList="dropdownList"
          />
        </UiFormItem>
        <div :class="css.buttonWrapper">
          <UiButton type="submit">   </UiButton>
        </div>
      </form>
    </div>
  </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex';
import Input from '@/components/UI/Input';
import FormItem from '@/components/UI/FormItem';
import Dropdown from '@/components/UI/Dropdown';
import Button from '@/components/UI/Button';
import WalletCard from '@/components/WalletCard';
import css from './CreateWalletPage.css';
export default {
  name: 'CreateWalletPage',
  data() {
    return {
      balance: '',
      type: null,
      dropdownList: [
        {
          id: 0,
          name: ''
        },
        {
          id: 1,
          name: ''
        },
        {
          id: 2,
          name: ' '
        },
        {
          id: 3,
          name: '  '
        }
      ],
      css
    };
  },
  computed: {
    ...mapState({
      wallet: state => state.walletStore.wallet
    }),
    walletName: {
      get() {
        return this.wallet.name;
      },
      set(newName) {
        return this.SET_WALLET_NAME(newName);
      }
    },
    walletType: {
      get() {
        return this.wallet.type;
      },
      set(newType) {
        return this.SET_WALLET_TYPE(newType);
      }
    },
    walletBalance: {
      get() {
        return this.wallet.balance;
      },
      set(newBalance) {
        return this.SET_WALLET_BALANCE(newBalance);
      }
    }
  },
  methods: {
    ...mapMutations([
      'SET_WALLET_NAME',
      'SET_WALLET_TYPE',
      'SET_WALLET_BALANCE'
    ]),
    submit() {
      alert('submit');
    }
  },
  mounted() {
    console.log(this.wallet);
  },
  components: {
    WalletCard,
    UiInput: Input,
    UiFormItem: FormItem,
    UiDropdown: Dropdown,
    UiButton: Button
  }
};
</script>
